<template>
    <div class="box">
        <div class="main">
            <ul>
                <li v-for="item in car" :key="item.id">
                    <van-cell-group>
                        <van-cell  title="单元格" value="内容" >
                            <template slot="title">
                                <van-checkbox v-model="item.isChecked" shape="square">{{ item.title }}{{ item.price }}</van-checkbox>
                            </template>
                            <van-stepper 
                            @click.native="upts(item.id)"
                            @change="changes" 
                            min="0" 
                            :max="item.maxVal"
                            v-model="item.num" />
                        </van-cell>
                    </van-cell-group>
                    
                    <!-- <input type="checkbox" v-model="item.isChecked"> -->
                    
                    <!-- <div>
                        <button @click="upt({id:item.id , val:-1})">-</button>
                        {{ item.num }}
                        <button @click="upt({id:item.id , val:1})">+</button>
                    </div> -->
                </li>
            </ul>
        </div>
        <div class="bot">
            <van-cell-group>
                <van-cell  title="单元格" value="内容" >
                    <template slot="title" >
                        <div class="aaa" @click="toggle" style="padding:0 20px;">
                            <van-checkbox disabled v-model="isAllChecked" shape="square">全选</van-checkbox>
                        </div>
                        
                    </template>
                    总价:{{ totalPrice }}
                </van-cell>
            </van-cell-group>
            <!-- <span>
                <van-checkbox v-model="isAllChecked" @click="toggle" shape="square">全选</van-checkbox>
               
            </span>
            <span>
                总价:{{ totalPrice }}
            </span> -->
        </div>
    </div>
</template>

<script>
import { mapState , mapMutations , mapActions , mapGetters } from "vuex"
export default {
    data() {
        return {
            currentId:"",
        };
    },
    computed: {
        ...mapState({
            car:function(state){
                return state.list.carlist
            }
        }),
        ...mapGetters({
            isAllChecked:"list/isAllChecked",
            totalPrice:"list/totalPrice"
        })
    },
    methods: {
        upts(val){
            console.info(val,123131);
            this.currentId = val;
        },
        changes(value){
            console.log(value);
            console.log(this.currentId,value)
            var that = this;
            setTimeout(()=>{
               that.upt({id:this.currentId,val:value})
            })
        },
        aa(){

        },
        ...mapActions({
            toggle:"list/toggle",
            
        }),
        ...mapMutations({
            upt:"list/upt"
        })
    },
    components: {

    },
};
</script>

<style scoped lang="scss">
.box{
    height: calc(100% - 50px);
    display: flex;
    flex-direction: column;
    .main{
        flex: 1;
        overflow: auto;
        li{
            display: flex;
            align-items: center;
        }
    }
    .bot{
        height: 50px;
        display: flex;
        justify-content: space-between;
        align-items: center;
    }
}
</style>
<style lang="scss" >
.main .van-cell-group{
    width: 100%;
}

.box .van-checkbox__label--disabled,.box  .van-radio__icon--disabled.van-radio__icon--checked .van-icon{
    color:#323233;
}

.box .van-checkbox__icon--disabled.van-checkbox__icon--checked .van-icon{
    color: #fff!important;
    background-color: #1989fa!important;
    border-color: #1989fa!important;
}
.box .van-checkbox__icon--disabled .van-icon{
color: transparent!important;
    background-color: transparent!important;
border: 1px solid #e5e5e5!important;
}
</style>
